h1, h2, h3, h4, h5, h6, input, textarea, p, td, a, label, div, button {
    font-family: IRANSans !important;
}

td, input, h3 {
    direction: rtl;
}


.ui.inverted.menu a.item:not(.active):hover {
    color: #ffb450 !important;
}
.ui.inverted.menu .active.item:hover {
    background: rgba(255,255,255,.15);
    /* color: #fff!important; */
}
.ui.inverted.menu a.item.active {
    transition: background-color .5s ease-out;
    background-color: #ff770e40 !important;
    color: #333333 !important;

}


.shooka.icon {
    font-size: 1.5em;
}

.shooka-pusher {
    transition: all .5s;
}

.shooka.sidebar {
    /*padding-top: 60px;*/
    overflow-y: unset !important;
    width: 240px !important;
    background-color: rgb(255, 255, 255) !important;
}

.shooka.ui.inverted.pointing.menu .active.item:after {
    background: #ffb450 !important;
}

.ui.dimmer {
    transition: all 0.3s ease;
    background: #00000000;
    /*background: transparent;*/
}

.modal-fade-in .ui.dimmer {
    background: #000000bb;
}

.ui.grid.shooka-100-columns > .row > .column {
    width: 1%;
}

